import { Component,Input, OnInit } from '@angular/core';
import {Doc} from './Models/doc';

@Component({
  selector: 'DocComponent',
  template: `
  <Table >
    <tr *ngIf="mydocument">
        <td class="text-center">
         <h3 >{{mydocument.name}}</h3>
            <pdf-viewer [src]="mydocument.path" 
                [render-text]="true"
                style="display: block;"
                [show-all]="false"
                [page]="mydocument.page">
            </pdf-viewer>
        </td>
    </tr>

    <tr  class="center-block">
        <td class="text-center" >
         <audio
            id="t-rex-roar"
            controls
            src={{mydocument.voicePath}}>
             Your browser does not support the <code>audio</code> element.
            </audio>
        </td>
    </tr>
    <Table>
  `
})
export class DocComponent implements OnInit {
  title = 'app';
  @Input() mydocument:Doc; 


  setTile(){
    //this.title =this.pdfSrc.replace("/^.*[\\\/]/", '');
    this.title = "this.mydocument.name;"
  }

  ngOnInit()
  {
      this.setTile();
  }

  
}





