import { Component, OnInit } from '@angular/core';
import { DOCS } from './Models/mock-docs';
import { Doc } from './Models/doc';
import {DocComponent} from './doc.component';

@Component({
  selector: 'app-root',
  template: `
  <div class="container">
  <table>
  <tr>
    <h3 class="text-center">My Words</h3>

  </tr>
    <tr>
        <td  class="text-center">
        <ul>
         <li *ngFor="let doc of documents" class="list-group-item" (click)="onSelect(doc)">
            <a>
              <span >{{doc.name}}</span>
            </a>
         </li>
        </ul>
        </td>
   </tr>

  <tr>
    <td class="text-center">
      <DocComponent [mydocument]="selectedDoc"></DocComponent>
    </td>
  </tr>

  </table>
  </div>
  `
})

export class AppComponent implements OnInit {
  documents = DOCS;
  selectedDoc: Doc;

  constructor()
  {

  }

  ngOnInit()
  {
    this.selectedDoc = this.documents[0];
  }

  onSelect(doc: Doc): void {
    this.selectedDoc = doc;
  }
}






